
#header{
    background-color: gray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 20px;
}
#Portfolio{
    padding-left: 3%;
    padding-top: 20px;
    font-size: 54px;
    color: white;
}
ul{
    display: flex;
    list-style: none;
    padding-right: 50px;
}
li{
    padding-right: 20px; 
}
#menu a{
    font-size: 20px;
    color: black;
    text-decoration: none;
}
#header-image{
    display: flex;
    justify-content: space-evenly;
}
#header-text{
    margin-top: 8%;
    margin-left: 2%;
    font-size: 30px;
}

#header-image img{
    width: 340px;
    height: 360px;
    padding: 60px;
    border-top-left-radius: 8pc;
    border-bottom-right-radius: 8pc;
    margin-top: 40px;
}

#about{
    margin-left:410px;
    margin-right:75px;
    width:50% ;
}

#about p{
    font-size: 200%;
    padding : 30px;
