    border-top-right-radius: 80px;
    border-bottom-left-radius: 80px;
    background-color: yellowgreen;
    
}
#center{
    text-align: center;
    font-weight: 300;
    font-size: 70px;

}
#cards{
    display: flex;
    margin-left: 20px;
    font-size: 10px;

}
