.contact-details{
    display: flex;
    margin: auto;
    height: 450px;
    width: 400px;
    background-color: antiquewhite;
    justify-content: space-evenly;
    border-radius: 60px;
    padding-bottom: 20px;
    
    
}
.contact-details:hover{
    background-color: rgb(186, 97, 126);
}
form{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 20px;
}
form h2{
    font-weight: 600;
    font-size: 40px;
    margin-bottom: 5px;
    color: black;
    margin-left: 60px;
}
form hr{
    border: none;
    width: 120px;;
    height: 5px;
    background-color: purple;
    border-radius: 10px;
    margin-bottom: 20px;
    margin-left: 110px;
}
#contact{
    margin-top: 50px;
}

.contact-name{
    width: 300px;
    height: 50px;
    border: none;
    outline: none;
    padding-left: 25px;
    font-weight: 500;
    color:gray;
    border-radius: 50px;
}
input{
    padding: 10px 20px;
}
.contact-name:focus{
    border: 2px solid orange;
}
textarea{
    height: 340px;
    width: 300px;
    padding-top: 15px;
    border-radius: 40px;
    padding-left: 30px;
    
}
textarea:focus{
    border: 2px solid orange;
}
button{
    margin-left: 110px;
    margin-bottom: 10px;
    padding: 15px 30px;
    font-size: 16px;
    color: antiquewhite;
    gap: 10px;
    border: none;
    border-radius: 50px;
    background:linear-gradient(270deg,purple,blue);
    cursor: pointer;
}

.social-media{
    margin-top: 20px;
     text-align: center;
